<template>
  <div> 
    <div class="topTitle">
      <span class="topTitle-span1">机器学习</span>
      <span class="topTitle-span1">
        <img src="../../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">样本数据集</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>样本数据
          <div class="button-content">
            
          </div>
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody">
                  <tr>
                    <td>{{index+1}}</td>
                    <td>{{item.sampleFileName}}</td>
                    <td>{{item.sampleTable}}</td>                  
                    <td>{{item.sampleNum}}</td>
                    <td>{{item.createTime}}</td>
                    <td>{{item.creater}}</td>
                    <td>{{item.updateTime}}</td>
                    <td>{{item.updater}}</td>
                    <td>
                      <span class="spanBox" @click="browseData(item.id)">数据浏览</span>                     
                    </td>
                   </tr>
                </template>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
    </div>
    <Modal :mask-closable="false"   v-model="showModal" :title="modelTitle" @on-cancel="cancel" class="dialog" :styles="{width: '80%'}">
      <component :is="tabview" @cancel="cancelDialog" @save="saveDatas" :dataObj="dataObj" width="80%"></component>
      <div slot="footer"></div>
    </Modal>  
  </div>
</template>
<script>
import browseData from "@/components/modelManage/machineLearnManage/dataMangage/browseData";
export default {
  components: {
    browseData
  },
  created() {
    this.axios
        .get("/sample/list")
        .then(response => {
            if (response.data) {
                this.tableBody = response.data;
            }
        })
        .catch(error => {});
  },
  data() {
    return {
      tabview:'',
      showModal: false,
      dataObj:{
      },
      modelTitle: "",        
      tableHeader: [
        { name: "序" },
        { name: "样本文件" },
        { name: "样本表" },
        { name: "数据量" },
        { name: "创建时间" },
        { name: "创建人" },
        { name: "更新时间" },
        { name: "更新人" },
        { name: "操作" },
      ],
      tableBody: [{}]
    };
  },
  mounted() {},
  methods:{
    cancel() {},
    browseData(id){
        this.modelTitle = '数据浏览'
        this.tabview = 'browseData';
        this.showModal = true;
        this.dataObj = {id:id};
    },
    saveDatas(){},
    cancelDialog(){}
  }
};
</script>



